<template>
  <div id="table">
  <el-table
  empty-text="No Suggestion"
    :data="tableData"
    style="width: 100%"
    :default-sort = "{prop: 'timestamp', order: 'descending'}">
    <el-table-column
      prop="status"
      label="STATUS"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="suggestion"
      label="SUGGESTION"
      sortable
      >
    </el-table-column>
    <el-table-column
      prop="timestamp"
      label="SENT"
      sortable
      width="180"
      class-name="entry">
    </el-table-column>
    <el-table-column
      prop="timestamp"
      label="UPDATED"
      sortable
      width="180">
    </el-table-column>
    <el-table-column
      prop="entry"
      label="ENTRIES"
      sortable
      width="120">
    </el-table-column>
  </el-table>
  </div>
</template>

<script>
  import axios from "axios"
  export default {
    data() {
      return {
        tableData: [],
        raw: '',
      }
    },
    methods: {
      formatter(row, column) {
        return row.address;
      }
    },
    created() {
      axios.get('http://35.197.160.94:8094/doctor/suggestions', {
        params: {
          uidPatient: this.$store.state.patient_id
        }
      }).then((response => {
        this.raw = response.data
        this.raw.forEach(el => {
          this.tableData.push(el)
        })
      }))
    }
  }
</script>

<style>
  .entry {
    text-align: center !important;
  }
.el-table img {
  height: 16px;
}
.el-table__body tr,
  .el-table__body td {
    padding: 0;
    height: 60px;
}
#table {
}
</style>


